import Vue from 'vue';
import Vuex from 'vuex';
import quiz from './modules/quiz';
import chapter from './modules/chapter';
import branches from './modules/branches';
import questions from './modules/questions';
import answer from './modules/answer';
import pairImages from './modules/chapters/pairImages';
import singleChoice2 from './modules/chapters/singleChoice2';
import timeline from './modules/chapters/timeline';
import { TIMELINE_SHOW_ANSWER } from './mutation-types';

Vue.use(Vuex);

export default new Vuex.Store({

  // Initial state
  state: {
    timelineShowAnswer: false,
  },

  // Mutations
  mutations: {
    [TIMELINE_SHOW_ANSWER](state, showAnswer) {
      state.timelineShowAnswer = showAnswer;
    },
  },

  // Modules
  modules: {
    quiz,
    chapter,
    branches,
    questions,
    answer,
    pairImages,
    singleChoice2,
    timeline,
  },
});
