import helpers from '../../helpers/helpers';

// Initial state
const state = {
  questions: [],
  options: [],
  answers: [],
  currentQuestion: {},
  currentOptions: [],
  currentOption: {},
  correctAnswers: [],
  correctAnswer: {},
  remainingQuestions: [],
  remainingOptions: [],
  remainingAnswers: [],
  selectedQuestion: {},
  selectedOptions: [],
  selectedOption: {},
  usedQuestions: [],
  usedOptions: [],
  usedAnswers: [],
  isCorrectAnswer: false,
};

// Getters
const getters = {};

// Actions
const actions = {
  shuffleQuestions({ commit }) {
    commit('shuffleQuestions');
  },

  shuffleOptions({ commit }) {
    commit('shuffleOptions');
  },

  getRandomQuestion({ commit, state }) {
    const question = state.questions[Math.floor(Math.random() * state.questions.length)];
    commit('setCurrentQuestion', question);
  },

  getPreviousQuestion({ commit, state }) {
    const questionCount = state.questions.length;
    const lastIndex = questionCount > 0 ? questionCount - 1 : 0;
    const currentIndex = state.questions.findIndex(el => el.id === state.currentQuestion.id);
    const question = currentIndex === 0 ? state.questions[lastIndex] : state.questions[currentIndex - 1];
    commit('setCurrentQuestion', question);
  },

  getNextQuestion({ commit, state }) {
    const questionCount = state.questions.length;
    const lastIndex = questionCount > 0 ? questionCount - 1 : 0;
    const currentIndex = state.questions.findIndex(el => el.id === state.currentQuestion.id);
    const question = currentIndex === lastIndex ? state.questions[0] : state.questions[currentIndex + 1];
    commit('setCurrentQuestion', question);
  },

  getPreviousOption({ commit, state }) {
    const optionCount = state.options.length;
    const lastIndex = optionCount > 0 ? optionCount - 1 : 0;
    const currentIndex = state.options.findIndex(el => el.id === state.currentOption.id);
    const option = currentIndex === 0 ? state.options[lastIndex] : state.options[currentIndex - 1];
    commit('setCurrentOption', option);
  },

  getNextOption({ commit, state }) {
    const optionCount = state.options.length;
    const lastIndex = optionCount > 0 ? optionCount - 1 : 0;
    const currentIndex = state.options.findIndex(el => el.id === state.currentOption.id);
    const option = currentIndex === lastIndex ? state.options[0] : state.options[currentIndex + 1];
    commit('setCurrentOption', option);
  },

  submitAnswer({ dispatch, commit, state }) {
    commit('submitAnswer');
    const usedQuestion = state.currentQuestion;
    const correctAnswer = state.answers.find(answer => answer.questionId === state.currentQuestion.id);
    const isCorrectAnswer = (correctAnswer.optionId === state.currentOption.id);
    if (isCorrectAnswer) {
      commit('isCorrectAnswer');
    } else {
      commit('isWrongAnswer');
    }
    dispatch('getNextQuestion');
    commit('setUsedQuestion', usedQuestion);
  },

  submitMatchImageAnswer({ dispatch, commit, state }) {
    commit('submitAnswer');
    const usedQuestion = state.currentQuestion;
    const usedOption = state.currentOption;
    const correctAnswer = state.answers.find(answer => answer.questionId === state.currentQuestion.id);
    const isCorrectAnswer = (correctAnswer.optionId === state.currentOption.id);
    if (isCorrectAnswer) {
      commit('isCorrectAnswer');
      dispatch('getNextQuestion');
      dispatch('getNextOption');
      commit('setUsedQuestion', usedQuestion);
      commit('setUsedOption', usedOption);
    } else {
      commit('isWrongAnswer');
    }
  },

  submitPairImagesAnswer({ commit, state }) {
    commit('submitAnswer');
    const usedQuestion = state.selectedQuestion;
    const correctAnswer = state.answers.find(answer => answer.questionId === usedQuestion.id);
    const correctOption = state.options.find(option => option.id === correctAnswer.optionId);
    const isCorrectAnswer = (correctAnswer.optionId === state.selectedOption.id);
    if (isCorrectAnswer) {
      commit('isCorrectAnswer');
      commit('setCorrectlyAnsweredQuestion', usedQuestion);
      commit('setCorrectlyUsedOption', correctOption);
    } else {
      commit('isWrongAnswer');
      commit('setWronglyAnsweredQuestion', usedQuestion);
      commit('setWronglyUsedOption', correctOption);
    }
    commit('setSelectedQuestion', {});
    commit('setSelectedOption', {});
    commit('setUsedQuestion', usedQuestion);
    commit('setUsedOption', correctOption);
  },

  submitSingleChoiceAnswer({ commit, state }, option) {
    commit('submitAnswer');
    const isCorrectAnswer = (state.correctAnswer.optionId === option.id);
    if (isCorrectAnswer) {
      commit('isCorrectAnswer');
    } else {
      commit('isWrongAnswer');
    }
  },
};

// Mutations
const mutations = {
  setQuestions(state, questions) {
    state.questions = questions.map(question => ({
      ...question,
      isSelected: false,
      isAnswered: false,
      isCorrectlyAnswered: null,
    }));
  },

  setOptions(state, options) {
    state.options = options.map(option => ({
      ...option,
      isSelected: false,
      isUsed: false,
      isCorrectlyUsed: null,
    }));
  },

  setAnswers(state, answers) {
    state.answers = answers;
  },

  shuffleQuestions(state) {
    state.questions = helpers.shuffle(state.questions);
  },

  shuffleOptions(state) {
    state.options = helpers.shuffle(state.options);
  },

  setCurrentQuestion(state, question) {
    state.currentQuestion = question;

    if (typeof question.options !== 'undefined' && question.options.length > 0) {
      state.currentOptions = question.options;
    }

    if (typeof question.answer !== 'undefined') {
      state.correctAnswer = question.answer;
    }
  },

  setSelectedQuestion(state, selectedQuestion) {
    state.questions = state.questions.map(question => ({ ...question, isSelected: false }));
    if (typeof selectedQuestion.id !== 'undefined') {
      const index = state.questions.findIndex(question => question.id === selectedQuestion.id);
      state.questions[index].isSelected = true;
    }
    state.selectedQuestion = selectedQuestion;
  },

  setSelectedOption(state, selectedOption) {
    state.options = state.options.map(option => ({ ...option, isSelected: false }));
    if (typeof selectedOption.id !== 'undefined') {
      const index = state.options.findIndex(option => option.id === selectedOption.id);
      state.options[index].isSelected = true;
    }
    state.selectedOption = selectedOption;
  },

  setCurrentOptions(state, options) {
    state.currentOptions = options;
  },

  setCurrentOption(state, option) {
    state.currentOption = option;
  },

  setCorrectAnswers(state, answers) {
    state.correctAnswers = answers;
  },

  setCorrectAnswer(state, answer) {
    state.correctAnswer = answer;
  },

  setUsedQuestion(state, usedQuestion) {
    const index = state.questions.findIndex(question => question.id === usedQuestion.id);
    state.questions[index].isAnswered = true;
    state.usedQuestions.push(usedQuestion);
    state.remainingQuestions = state.remainingQuestions.filter(question => question.id !== usedQuestion.id);
  },

  setUsedOption(state, usedOption) {
    const index = state.options.findIndex(option => option.id === usedOption.id);
    state.options[index].isUsed = true;
    state.usedOptions.push(usedOption);
    state.remainingOptions = state.remainingOptions.filter(option => option.id !== usedOption.id);
  },

  setUsedAnswer(state, usedAnswer) {
    state.usedAnswers.push(usedAnswer);
    state.remainingAnswers = state.remainingAnswers.filter(answer => answer.id !== usedAnswer.id);
  },

  submitAnswer() {
  },

  isCorrectAnswer(state) {
    state.isCorrectAnswer = true;
  },

  isWrongAnswer(state) {
    state.isCorrectAnswer = false;
  },

  setCorrectlyAnsweredQuestion(state, answeredQuestion) {
    const index = state.questions.findIndex(question => question.id === answeredQuestion.id);
    state.questions[index].isCorrectlyAnswered = true;
  },

  setWronglyAnsweredQuestion(state, answeredQuestion) {
    const index = state.questions.findIndex(question => question.id === answeredQuestion.id);
    state.questions[index].isCorrectlyAnswered = false;
  },

  setCorrectlyUsedOption(state, usedOption) {
    const index = state.options.findIndex(option => option.id === usedOption.id);
    state.options[index].isCorrectlyUsed = true;
  },

  setWronglyUsedOption(state, usedOption) {
    const index = state.options.findIndex(option => option.id === usedOption.id);
    state.options[index].isCorrectlyUsed = false;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
