// Initial state
const state = {
  questionId: null,
  questionTitle: null,
  options: [],
  answer: {},
};

// Getters
const getters = {};

// Actions
const actions = {
  initQuestion({ commit }, request) {
    commit('setQuestionId', request.question.id);
    commit('setQuestionTitle', request.question.title);
    commit('setOptions', request.question.options);
    commit('setAnswer', request.question.answer);
  },

  submitAnswer({ commit, dispatch, state }, option) {
    let isCorrect = false;
    const correctOption = state.options.find(currOption => currOption.id === state.answer.optionId);

    if (state.answer.optionId === option.id) {
      commit('isCorrectAnswer', option);
      isCorrect = true;
    } else {
      commit('isWrongAnswer', option);
    }

    // Time to reveal the answer! For this we will hand over responsibility
    // to the module with all the answers.
    dispatch('answer/show', {
      isCorrect,
      description: state.answer.description,
      image: state.answer.image,
      correctAnswer: correctOption.title,
    }, { root: true });
  },
};

// Mutations
const mutations = {
  setQuestionId(state, questionId) {
    state.questionId = questionId;
  },

  setQuestionTitle(state, questionTitle) {
    state.questionTitle = questionTitle;
  },

  setOptions(state, options) {
    state.options = options.map(option => ({
      ...option,
      isRight: null,
      isWrong: null,
    }));
  },

  setAnswer(state, answer) {
    state.answer = answer;
  },

  isCorrectAnswer(state, option) {
    const index = state.options.findIndex(currOption => currOption.id === option.id);
    state.options[index].isRight = true;
  },

  isWrongAnswer(state, option) {
    const index = state.options.findIndex(currOption => currOption.id === option.id);
    state.options[index].isWrong = true;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
