import api from '../../api/api';

// Initial state
const state = {
  quizId: null,
  quizName: null,
  chapters: [],
  currentChapterId: null,
  isCompleted: false,
};

// Getters
const getters = {};

// Actions
const actions = {
  async startQuiz({ dispatch, commit }, request) {
    let quiz;

    try {
      quiz = await api.getQuizById(request.quizId);
    } catch (e) {
      commit('requestQuizError', e);
      return;
    }

    commit('setQuizId', quiz.id);
    commit('setQuizName', quiz.id);
    commit('setChapters', quiz.chapters);

    // All set to get started, hand over responsibility of selecting a
    // chaper to run.
    dispatch('nextChapter', request.chapterId);
  },

  nextChapter({ commit, dispatch, state }, chapterId) {
    const chapterCount = state.chapters.length;
    const lastIndex = chapterCount > 0 ? chapterCount - 1 : -1;

    // By default we assume that the next chapter to run has index 0.
    let nextIndex = 0;

    // Check if we have actually been completing a chapter already and in
    // such case find the index of the next chapter to run.
    if (state.currentChapterId !== null) {
      nextIndex = state.chapters.findIndex(el => el.id === state.currentChapterId) + 1;
    }

    // For debug purposes it is possible to jump straigth to a specific chapter.
    // Check if a specific chapter has been requested.
    if (chapterId !== null) {
      nextIndex = state.chapters.findIndex(el => el.id === chapterId);
    }

    // Check if we have completed all chapters.
    if (nextIndex > lastIndex) {
      commit('setIsCompleted', true);
      return;
    }

    // At this point we know there are remaining chapters to run so lets grab
    // the next one!
    const chapter = state.chapters[nextIndex];

    // Update the current chapter ID so we remeber how far into the quiz we
    // have come.
    commit('setCurrentChapterId', chapter.id);

    // Notify the chapter module that there is another chapter to run.
    dispatch('chapter/startChapter', chapter, { root: true });
  },
};

// Mutations
const mutations = {
  requestQuizError() {
  },

  quizCompleted() {
  },

  setQuizId(state, quizId) {
    state.quizId = quizId;
  },

  setQuizName(state, quizName) {
    state.quizName = quizName;
  },

  setChapters(state, chapters) {
    state.chapters = chapters;
  },

  setCurrentChapterId(state, chapterId) {
    state.currentChapterId = chapterId;
  },

  setIsCompleted(state, isCompleted) {
    state.isCompleted = isCompleted;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
