export default {
  async getQuizById(id, flags) {
    flags = this._resolveFlags(flags);
    await this._sleep(flags.timeout);
    this._randomError(flags.failRate);

    return _quizzes.find(quiz => quiz.id === id);
  },

  async getChapterById(quizId, chapterId, flags) {
    flags = this._resolveFlags(flags);
    await this._sleep(flags.timeout);
    this._randomError(flags.failRate);

    const quiz = await this.getQuizById(quizId, { timeout: 0, failRate: 0 });
    return quiz.chapters.find(chapter => chapter.id === chapterId);
  },

  _sleep(timeout) {
    return new Promise((resolve) => {
      setTimeout(resolve, timeout);
    });
  },

  _resolveFlags(flags) {
    return { ..._flags, ...flags };
  },

  _randomError(failRate) {
    if (Math.random() < failRate && !navigator.webdriver) {
      throw new Error();
    }
  },
};
