// Initial state
const state = {
  isActive: false,
  isCorrect: null,
  correctAnswer: '',
  description: '',
  image: null,
};

// Getters
const getters = {};

// Actions
const actions = {
  show({ commit }, answer) {
    commit('setIsActive', true);
    commit('setIsCorrect', answer.isCorrect);
    commit('setCorrectAnswer', answer.correctAnswer);
    commit('setDescription', answer.description);
    commit('setImage', answer.image);
  },

  hide({ commit }) {
    commit('setIsActive', false);
    commit('setIsCorrect', null);
    commit('setCorrectAnswer', '');
    commit('setDescription', '');
    commit('setImage', null);
  },
};

// Mutations
const mutations = {
  setIsActive(state, isActive) {
    state.isActive = isActive;
  },

  setIsCorrect(state, isCorrect) {
    state.isCorrect = isCorrect;
  },

  setCorrectAnswer(state, correctAnswer) {
    state.correctAnswer = correctAnswer;
  },

  setDescription(state, description) {
    state.description = description;
  },

  setImage(state, image) {
    state.image = image;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
