// Initial state
const state = {
  chapterId: null,
  chapterName: null,
  chapterType: null,
  chapterPosition: null,
  questions: [],
  options: [],
  answers: [],
  currentQuestionId: null,
};

// Getters
const getters = {};

// Actions
const actions = {
  startChapter({ commit }, chapter) {
    commit('setChapterId', chapter.id);
    commit('setChapterName', chapter.name);
    commit('setChapterType', chapter.type);
    commit('setChapterPosition', chapter.position);
    commit('setQuestions', chapter.questions);

    // Alright, so here it becomes a bit tricky since the data structure of
    // chapters can differ. To cope with this each chapter has its own module
    // who knows exactly how to play the chapter. For now we will just check if
    // what properties the chapter has and store them here.
    if (typeof chapter.options !== 'undefined') {
      commit('setOptions', chapter.options);
    }

    if (typeof chapter.answers !== 'undefined') {
      commit('setAnswers', chapter.answers);
    }
  },

  endChapter({ dispatch }) {
    dispatch('quiz/nextChapter', null, { root: true });
  },

  nextQuestion({ commit, dispatch, state }, params) {
    // Set defaults for supported params.
    params = {
      skipLastQuestion: false,
      ...params,
    };

    const questionCount = state.questions.length;
    const lastIndex = questionCount > 0 ? questionCount - 1 : -1;

    // By default we assume that the next question to run has index 0.
    let nextIndex = 0;

    // Check if we have actually been completing a chapter already and in
    // such case find the index of the next chapter to run.
    if (state.currentQuestionId !== null) {
      nextIndex = state.questions.findIndex(el => el.id === state.currentQuestionId) + 1;
    }

    // If we have arrived at the last question of the chapter, check if we
    // have been instructed to skip that question. Woot!? Why would you ever
    // want to skip a question you say - well, this is useful in quizzes
    // where you are supposed to pair things together, say you are pairing
    // images and you have arrived at the last question, well, you'd only
    // have a single option to chose from. That's a bit too easy, don't you
    // think?
    if (nextIndex === lastIndex && params.skipLastQuestion === true) {
      dispatch('endChapter');
      return;
    }

    // Check if we have completed all questions in this chapter and if we did
    // hand over the responsibility to determine what to do to someone else.
    if (nextIndex > lastIndex) {
      dispatch('endChapter');
      return;
    }

    // Still questions left? Cool, let's load the next one!
    const question = state.questions[nextIndex];

    // Update the current question ID so we remeber how far into the chapter
    // we have come.
    commit('setCurrentQuestionId', question.id);

    // Here we hand over the responsibility to load the next question to the
    // real expert, namely the chapter type module in charge.
    // Did you notice how we pass in all questions even though we singled out
    // the next question to use? We do so as some chapter type modules might
    // end up using every question in the chapter at once or need the info
    // for some other funky purpose we know little about at this stage.
    dispatch(`${state.chapterType}/initQuestion`, {
      question,
      questions: state.questions,
      options: state.options,
      answers: state.answers,
    }, { root: true });
  },
};

// Mutations
const mutations = {
  setChapterId(state, chapterId) {
    state.chapterId = chapterId;
  },

  setChapterName(state, chapterName) {
    state.chapterName = chapterName;
  },

  setChapterType(state, chapterType) {
    state.chapterType = chapterType;
  },

  setChapterPosition(state, chapterPosition) {
    state.chapterPosition = chapterPosition;
  },

  setQuestions(state, questions) {
    state.questions = questions;
  },

  setOptions(state, options) {
    state.options = options;
  },

  setAnswers(state, answers) {
    state.answers = answers;
  },

  setCurrentQuestionId(state, questionId) {
    state.currentQuestionId = questionId;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
