import api from '../../api/api';
import helpers from '../../helpers/helpers';

// Initial state
const state = {
  branches: [],
  branch: {},
};

// Getters
const getters = {};

// Actions
const actions = {
  async getBranchById({ dispatch, commit }, request) {
    try {
      commit('getBranchRequest');
      const game = await api.getGameById(request.gameId);
      const branch = game.branches.find(currBranch => currBranch.id === request.branchId);
      dispatch('games/resolveGame', game, { root: true });
      dispatch('resolveBranch', branch);
    } catch (e) {
      commit('getBranchError');
      throw e;
    }
  },

  getNextBranch({ dispatch, commit, state }) {
    commit('getBranchRequest');

    // No branches in the game.
    if (state.branches.length === 0) {
      commit('games/gameCompleted', null, { root: true });
      return;
    }

    // First branch in the game.
    if (typeof state.branch.position === 'undefined') {
      dispatch('resolveBranch', state.branches[0]);
      return;
    }

    // Find next branch in the game.
    // Assumes branches are ordered according to position ascending.
    const branch = state.branches.find(nextBranch => nextBranch.position > state.branch.position);

    if (typeof branch !== 'undefined') {
      dispatch('resolveBranch', branch);
      return;
    }

    // No branches left in the game.
    commit('games/gameCompleted', null, { root: true });
  },

  // Set chapters of the game and select the first chapter.
  setBranches({ dispatch, commit }, request) {
    commit('setBranches', request.branches);

    // For debug purposes it is possible to jump straigth to a specific branch.
    // Check if a specific branch has been specified.
    if (request.branchId !== null) {
      const branch = request.branches.find(currentBranch => currentBranch.id === request.branchId);
      dispatch('resolveBranch', branch);
      return;
    }

    // Check that a chapter exists.
    if (typeof request.branches[0] !== 'undefined') {
      dispatch('resolveBranch', request.branches[0]);
    }
  },

  // Set chapter including associated questions, options and answers.
  resolveBranch({ commit }, branch) {
    commit('setBranch', branch);

    if (typeof branch.questions !== 'undefined') {
      commit('questions/setQuestions', branch.questions, { root: true });
    }

    if (typeof branch.options !== 'undefined') {
      commit('questions/setOptions', branch.options, { root: true });
    }

    if (typeof branch.answers !== 'undefined') {
      commit('questions/setAnswers', branch.answers, { root: true });
    }
  },
};

// Mutations
const mutations = {
  getBranchRequest() {
  },

  getBranchError() {
  },

  setBranches(state, branches) {
    state.branches = helpers.formatBranches(branches);
  },

  setBranch(state, branch) {
    state.branch = helpers.formatBranch(branch);
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
